import { useEffect } from "react";
import { useLocation, useNavigate } from "react-router-dom";

const ScrollToTop = () => {
  const location = useLocation();
  const navigate = useNavigate();
  const scrollToFooter = location.state?.scrollToFooter;

  useEffect(() => {
    if (scrollToFooter) {
      let attempts = 0;

      const tryScrollToFooter = () => {
        const footer = document.getElementById("footer");
        if (footer) {
          document.documentElement.style.scrollBehavior = "auto";
          footer.scrollIntoView({ behavior: "auto", block: "start" });
          document.documentElement.style.scrollBehavior = "";

          navigate(location.pathname, { replace: true, state: {} });
        } else if (attempts < 10) {
          attempts++;
          setTimeout(tryScrollToFooter, 100); // Retry until footer exists
        }
      };

      tryScrollToFooter();
      return;
    }

    // Only scroll to top if not going to footer
    window.scrollTo({ top: 0, behavior: "auto" });
  }, [location.pathname, location.state, navigate]);

  return null;
};

export default ScrollToTop;
