// ----- Motion Variants -----
const fadeIn: Variants = {
  hidden: { opacity: 0, y: 48 },
  visible: (i: number = 0): TargetAndTransition => ({
    opacity: 1,
    y: 0,
    transition: {
      delay: i * 0.08,
      type: "spring",
      stiffness: 120,
    },
  }),
};
const popIn: Variants = {
  hidden: { opacity: 0, scale: 0.85, y: 10 },
  visible: (i: number = 0): TargetAndTransition => ({
    opacity: 1,
    scale: 1,
    y: 0,
    transition: {
      delay: i * 0.06,
      type: "spring",
      stiffness: 300,
    },
  }),
};
const bounceTap: Variants = {
  tap: { scale: 0.97, boxShadow: "0 5px 20px 0 #c084fc80" },
};
const cardGlow: Variants = {
  rest: { boxShadow: "0 8px 32px rgba(180,138,255,0.25)", scale: 1 },
  hover: { boxShadow: "0 22px 50px #c084fc66", scale: 1.04 },
};
const popupAnim: Variants = {
  hidden: { opacity: 0, y: -22, scale: 0.93 },
  visible: { opacity: 1, y: 0, scale: 1 },
  exit: { opacity: 0, y: -12, scale: 0.97 },
};

