const InfoCard: React.FC<InfoCardProps> = ({ data }) => {
  const [expanded, setExpanded] = useState<boolean>(false);
  const isMobile =
    typeof window !== "undefined" ? window.innerWidth < 1024 : false;
  const points = isMobile ? data.pointsMobile : data.pointsDesktop;

  return (
    <motion.div
      className={`info-card glass-card ${expanded ? "expanded" : ""}`}
      layout
      variants={fadeIn}
      initial="hidden"
      whileInView="visible"
      viewport={{ once: true, amount: 0.2 }}
    >
      <h4 className="centered-heading">{data.title}</h4>
      {isMobile && <p className="info-short">{data.short}</p>}
      {isMobile ? (
        <>
          <AnimatePresence>
            {expanded && (
              <motion.ul
                className="bullet-points"
                initial="hidden"
                animate="visible"
                exit="hidden"
                variants={{
                  hidden: { opacity: 0, height: 0 },
                  visible: {
                    opacity: 1,
                    height: "auto",
                    transition: {
                      when: "beforeChildren",
                      staggerChildren: 0.06,
                    },
                  },
                }}
              >
                {points.map((point: string | LottiePoint, i: number) => {
                  if (typeof point === "string") {
                    return (
                      <motion.li variants={popIn} custom={i} key={i}>
                        <span className="bullet-check">•</span>
                        {point}
                      </motion.li>
                    );
                  } else if ((point as LottiePoint).type === "lottie") {
                    const lottiePoint = point as LottiePoint;
                    return (
                      <motion.div
                        className={`lottie-center-block ${
                          lottiePoint.className || ""
                        }`}
                        key={i}
                      >
                        <p
                          className="tagline colorful-tagline"
                          dangerouslySetInnerHTML={{
                            __html: lottiePoint.tagline,
                          }}
                        ></p>
                        <div className="lottie-centered">
                          <DotLottieReact
                            src={lottiePoint.lottie}
                            autoplay
                            loop
                          />
                        </div>
                      </motion.div>
                    );
                  }
                  return null;
                })}
              </motion.ul>
            )}
          </AnimatePresence>
          <motion.button
            className="collapse-btn"
            onClick={() => setExpanded((p) => !p)}
            whileHover={{
              scale: 1.07,
              backgroundColor: "#c084fc20",
              color: "#c084fc",
            }}
            whileTap={{ scale: 0.96 }}
          >
            {expanded ? "Show Less ▲" : "Show More ▼"}
          </motion.button>
        </>
      ) : (
        <ul className="bullet-points desktop-points">
          {points.map((point: string | LottiePoint, i: number) => {
            if (typeof point === "string") {
              return (
                <li key={i}>
                  <span className="bullet-check">•</span>
                  {point}
                </li>
              );
            } else if ((point as LottiePoint).type === "lottie") {
              const lottiePoint = point as LottiePoint;
              return (
                <div
                  key={i}
                  className={`lottie-center-block ${
                    lottiePoint.className || ""
                  }`}
                >
                  <p
                    className="tagline colorful-tagline"
                    dangerouslySetInnerHTML={{ __html: lottiePoint.tagline }}
                  ></p>
                  <div className="lottie-centered">
                    <DotLottieReact src={lottiePoint.lottie} autoplay loop />
                  </div>
                </div>
              );
            }
            return null;
          })}
        </ul>
      )}
    </motion.div>
  );
};

