import { useNavigate } from "react-router-dom";
import "./Paymentstyles.css";

const PaymentSuccess = () => {
  const navigate = useNavigate();

  return (
    <div className="payment-status-bg">
      <section className="payment-status-panel animate-fadeIn">
        {/* SVG Animated Checkmark Icon */}
        <div className="status-icon success">
          <svg viewBox="0 0 80 80" width="64" height="64">
            <defs>
              <linearGradient id="gradient" x1="0%" y1="0%" x2="100%" y2="100%">
                <stop offset="0%" stopColor="#c084fc" />
                <stop offset="60%" stopColor="#60a5fa" />
                <stop offset="100%" stopColor="#f472b6" />
              </linearGradient>
            </defs>
            <circle
              cx="40"
              cy="40"
              r="36"
              fill="none"
              stroke="url(#gradient)"
              strokeWidth="6"
              opacity="0.18"
            />
            <polyline
              points="24,44 37,58 58,28"
              fill="none"
              stroke="url(#gradient)"
              strokeWidth="7"
              strokeLinecap="round"
              strokeLinejoin="round"
              style={{
                strokeDasharray: 64,
                strokeDashoffset: 0,
                animation:
                  "checkmarkStroke 0.9s cubic-bezier(.56,1.58,.64,1) forwards",
              }}
            />
          </svg>
        </div>

        <h2 className="status-title gradient-text">Payment Complete!</h2>
        <p className="status-message">
          Your content journey just got a serious upgrade.{" "}
          <strong>Premium editing unlocked</strong> – let’s turn your vision
          into scroll-stopping content.
          <br />
          <span className="subtle-highlight">
            🚀 You’re all set to grow on autopilot
          </span>
        </p>

        <button
          onClick={() => navigate("/")}
          className="primary-button glow-on-hover"
          style={{
            marginTop: "2rem",
            background:
              "linear-gradient(90deg, #c084fc, #60a5fa 60%, #f472b6 100%)",
            color: "#fff",
            fontWeight: 700,
            border: "none",
            borderRadius: "0.7em",
            boxShadow: "0 2px 14px #c084fc40",
            padding: "1em 2.4em",
            fontSize: "1.25rem",
            cursor: "pointer",
            transition:
              "transform 0.14s cubic-bezier(0.72,1.85,.5,1), box-shadow 0.19s",
          }}
          onMouseDown={(e) => (e.currentTarget.style.transform = "scale(0.98)")}
          onMouseUp={(e) => (e.currentTarget.style.transform = "scale(1)")}
        >
          Go to Home
        </button>

        {/* Decorative Confetti (optional placeholder) */}
        <div className="confetti-animation"></div>
      </section>
    </div>
  );
};

export default PaymentSuccess;
