import { useNavigate } from "react-router-dom";
import "./Paymentstyles.css";

const PaymentFailed = () => {
  const navigate = useNavigate();

  return (
    <div className="payment-status-bg">
      <section className="payment-status-panel animate-fadeIn">
        {/* Animated SVG X-Cross Icon */}
        <div className="status-icon error error-animate-shake">
          <svg viewBox="0 0 80 80" width="64" height="64">
            <defs>
              <linearGradient
                id="fail-gradient"
                x1="0%"
                y1="0%"
                x2="100%"
                y2="100%"
              >
                <stop offset="0%" stopColor="#f472b6" />
                <stop offset="100%" stopColor="#c084fc" />
              </linearGradient>
            </defs>
            <circle
              cx="40"
              cy="40"
              r="36"
              fill="none"
              stroke="url(#fail-gradient)"
              strokeWidth="7"
              opacity="0.18"
            />
            <line
              x1="29"
              y1="29"
              x2="51"
              y2="51"
              stroke="url(#fail-gradient)"
              strokeWidth="7"
              strokeLinecap="round"
              style={{
                strokeDasharray: 40,
                strokeDashoffset: 0,
                animation:
                  "crossStroke 0.48s cubic-bezier(.69,1.52,.44,1) 0.14s both",
              }}
            />
            <line
              x1="51"
              y1="29"
              x2="29"
              y2="51"
              stroke="url(#fail-gradient)"
              strokeWidth="7"
              strokeLinecap="round"
              style={{
                strokeDasharray: 40,
                strokeDashoffset: 0,
                animation:
                  "crossStroke 0.55s cubic-bezier(.69,1.52,.44,1) 0.26s both",
              }}
            />
          </svg>
        </div>
        <h2 className="status-title gradient-text" style={{ color: "#f472b6" }}>
          Payment Failed
        </h2>
        <p className="status-message">
          Looks like we hit a creative snag.
          <br />
          <span className="subtle-highlight">
            But your journey doesn’t end here—retry and bring your next
            masterpiece to life.
          </span>
        </p>
        <button
          onClick={() => navigate("/")} // or payment page route
          className="primary-button glow-on-hover"
          style={{
            marginTop: "2rem",
            background: "linear-gradient(90deg, #f472b6, #c084fc 80%)",
            color: "#fff",
            fontWeight: 700,
            border: "none",
            borderRadius: "0.7em",
            boxShadow: "0 2px 16px #f472b664",
            padding: "1em 2.4em",
            fontSize: "1.25rem",
            cursor: "pointer",
            transition: "transform 0.13s, box-shadow 0.17s",
          }}
          onMouseDown={(e) => (e.currentTarget.style.transform = "scale(0.96)")}
          onMouseUp={(e) => (e.currentTarget.style.transform = "scale(1)")}
        >
          Try Again
        </button>
        {/* Decorative spark/particle, if desired */}
        <div className="failed-sparkle"></div>
      </section>
    </div>
  );
};

export default PaymentFailed;
